'use client';

import React, { useState, useEffect } from 'react';
import Template from './Template';
import {
  Box,
  Typography,
  Grid,
  Card,
  CardContent,
  Button,
  Avatar,
  Divider,
  Chip,
  LinearProgress,
  IconButton,
  Tooltip,
} from '@mui/material';
import {
  Warning,
  Settings,
  LocationCity,
  Terminal,
  People,
  Category,
  Inventory,
  DataArray,
  Build,
  Assignment,
  Refresh,
  Notifications,
  Dashboard as DashboardIcon,
} from '@mui/icons-material';
import { useRouter } from 'next/navigation';
import Cookies from 'js-cookie';

const primaryColor = '#00ED64'; // MongoDB Green
const secondaryColor = '#0A0F19'; // MongoDB Background
const accentColor = '#FF6B6B'; // Alert Red
const cardBackground = '#1e272e'; // Card Background
const darkCardBackground = '#2d3436'; // Darker Card Background

export default function DashboardPage() {
  const router = useRouter();
  const [lastUpdated, setLastUpdated] = useState(new Date());
  const [userProfile, setUserProfile] = useState<{ fullName?: string; phoneNumber?: string; image?: string } | null>(null);

  // Simulated real-time data updates
  useEffect(() => {
    const interval = setInterval(() => {
      setLastUpdated(new Date());
    }, 30000); // Update every 30 seconds

    return () => clearInterval(interval);
  }, []);

  // Fetch user profile
  useEffect(() => {
    const email = Cookies.get('email');
    if (!email) return;

    fetch(`/api/user-profile?email=${email}`)
      .then((res) => res.json())
      .then((data) => {
        if (data.success) {
          setUserProfile({
            fullName: data.fullName,
            phoneNumber: data.phoneNumber,
            image: data.image,
          });
        }
      })
      .catch((err) => console.error('Failed to fetch profile', err));
  }, []);

  const statistics = [
    {
      title: 'Total Stations',
      count: 42,
      icon: <LocationCity />,
      trend: '+2.5%',
      status: 'active',
    },
    {
      title: 'Connected Devices',
      count: 125,
      icon: <Terminal />,
      trend: '+1.2%',
      status: 'active',
    },
    {
      title: 'Active Alerts',
      count: 3,
      icon: <Warning />,
      trend: '-15%',
      status: 'warning',
    },
    {
      title: 'System Health',
      count: '98.5%',
      icon: <DashboardIcon />,
      trend: '+0.3%',
      status: 'excellent',
    },
  ];

  const quickActions = [
    {
      title: 'ACTIVE TRAINS',
      desc: 'Active train data',
      btn: 'See active trains',
      path: '/rms/active-trains',
      icon: <Category />,
      priority: 'medium',
    },
    {
      title: 'Assets Inventory',
      desc: 'Monitor and manage all IPIS hardware assets and their status.',
      btn: 'View Assets',
      path: '/rms/assets',
      icon: <Inventory />,
      priority: 'high',
    },
    {
      title: 'Activity Logs',
      desc: 'Comprehensive system activity logs and audit trails.',
      btn: 'View Activity Logs',
      path: '/rms/activity-log',
      icon: <Assignment />,
      priority: 'medium',
    },
    {
      title: 'Station Information',
      desc: 'Update and maintain railway station metadata and configurations.',
      btn: 'Station Info',
      path: '/rms/station-info',
      icon: <LocationCity />,
      priority: 'medium',
    },
    {
      title: 'System Settings',
      desc: 'Global system configurations, thresholds, and preferences.',
      btn: 'Open Settings',
      path: '/rms/settings',
      icon: <Settings />,
      priority: 'low',
    },
    {
      title: 'Alert Management',
      desc: 'Configure and monitor CAP alerts and emergency notifications.',
      btn: 'Manage Alerts',
      path: '/rms/cap-alerts',
      icon: <Notifications />,
      priority: 'high',
    },
  ];

  // Sort actions by priority
  const sortedActions = quickActions.sort((a, b) => {
    const priorityOrder = { high: 1, medium: 2, low: 3 };
    return priorityOrder[a.priority] - priorityOrder[b.priority];
  });

  const getStatusColor = (status: string) => {
    switch (status) {
      case 'excellent':
        return primaryColor;
      case 'active':
        return '#74b9ff';
      case 'warning':
        return accentColor;
      default:
        return '#95a5a6';
    }
  };

  const getPriorityColor = (priority: string) => {
    switch (priority) {
      case 'high':
        return accentColor;
      case 'medium':
        return '#f39c12';
      case 'low':
        return '#95a5a6';
      default:
        return '#95a5a6';
    }
  };

  const handleNavigation = (path: string) => {
    try {
      router.push(path);
    } catch (error) {
      console.error('Navigation error:', error);
    }
  };

  const handleRefresh = () => {
    setLastUpdated(new Date());
    // Add refresh logic here
  };

  return (
    <Template>
      <Box
        sx={{
          flexGrow: 1,
          display: 'flex',
          flexDirection: 'column',
          minHeight: '100vh',
          backgroundColor: secondaryColor,
          color: 'white',
          px: { xs: 2, md: 4 },
          py: 3,
        }}
      >
        {/* Header Section */}
        <Box display="flex" justifyContent="space-between" alignItems="center" mb={2}>
          <Box>
            <Typography
              variant="h4"
              gutterBottom
              sx={{
                color: primaryColor,
                fontWeight: 700,
                fontSize: { xs: '1.5rem', md: '2.125rem' },
              }}
            >
              IPIS Management Dashboard
            </Typography>
            <Typography variant="subtitle1" sx={{ opacity: 0.8, mb: 1 }}>
              IP-based Integrated Passenger Information System
            </Typography>
            <Typography variant="caption" sx={{ opacity: 0.6 }}>
              Last updated: {lastUpdated.toLocaleTimeString()}
            </Typography>
          </Box>
          {/* User Profile Info */}
          {userProfile && (
            <Box display="flex" alignItems="center" gap={2}>
              <Avatar
                src={userProfile.image || ''}
                alt={userProfile.fullName || 'User'}
                sx={{ width: 48, height: 48 }}
              />
              <Box>
                <Typography variant="subtitle1" fontWeight={600}>
                  {userProfile.fullName || 'N/A'}
                </Typography>
                <Typography variant="body2" sx={{ opacity: 0.7 }}>
                  {Cookies.get('email') || 'N/A'}
                </Typography>
                <Typography variant="body2" sx={{ opacity: 0.7 }}>
                  {userProfile.phoneNumber || 'N/A'}
                </Typography>
              </Box>
            </Box>
          )}
          <Tooltip title="Refresh Dashboard">
            <IconButton
              onClick={handleRefresh}
              sx={{
                color: primaryColor,
                '&:hover': { backgroundColor: 'rgba(0, 237, 100, 0.1)' },
              }}
            >
              <Refresh />
            </IconButton>
          </Tooltip>
        </Box>

        {/* System Health Progress */}
        <Box sx={{ mb: 3 }}>
          <Typography variant="body2" sx={{ mb: 1, opacity: 0.8 }}>
            System Health: 98.5%
          </Typography>
          <LinearProgress
            variant="determinate"
            value={98.5}
            sx={{
              height: 8,
              borderRadius: 4,
              backgroundColor: 'rgba(255,255,255,0.1)',
              '& .MuiLinearProgress-bar': {
                backgroundColor: primaryColor,
                borderRadius: 4,
              },
            }}
          />
        </Box>

        {/* Statistics Cards */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          {statistics.map((item, index) => (
            <Grid item xs={12} sm={6} md={3} key={index}>
              <Card
                sx={{
                  backgroundColor: cardBackground,
                  borderRadius: 3,
                  boxShadow: '0 8px 32px rgba(0,0,0,0.3)',
                  color: 'white',
                  border: `1px solid rgba(${item.status === 'warning' ? '255,107,107' : '0,237,100'}, 0.2)`,
                  transition: 'all 0.3s ease',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: '0 12px 40px rgba(0,0,0,0.4)',
                  },
                }}
              >
                <CardContent>
                  <Box display="flex" alignItems="center" justifyContent="space-between">
                    <Box display="flex" alignItems="center" gap={2}>
                      <Avatar
                        sx={{
                          bgcolor: getStatusColor(item.status),
                          width: 48,
                          height: 48,
                        }}
                      >
                        {item.icon}
                      </Avatar>
                      <Box>
                        <Typography variant="h5" fontWeight="bold">
                          {item.count}
                        </Typography>
                        <Typography variant="body2" sx={{ opacity: 0.8 }}>
                          {item.title}
                        </Typography>
                      </Box>
                    </Box>
                    <Chip
                      label={item.trend}
                      size="small"
                      sx={{
                        backgroundColor: item.trend.startsWith('+')
                          ? 'rgba(0,237,100,0.2)'
                          : 'rgba(255,107,107,0.2)',
                        color: item.trend.startsWith('+') ? primaryColor : accentColor,
                        fontWeight: 'bold',
                      }}
                    />
                  </Box>
                </CardContent>
              </Card>
            </Grid>
          ))}
        </Grid>

        <Divider sx={{ my: 4, borderColor: 'rgba(255,255,255,0.1)' }} />

        {/* Quick Actions Section */}
        <Box display="flex" alignItems="center" gap={2} mb={3}>
          <Typography
            variant="h5"
            sx={{
              color: primaryColor,
              fontWeight: 700,
              fontSize: { xs: '1.25rem', md: '1.5rem' },
            }}
          >
            System Modules
          </Typography>
          <Chip
            label={`${sortedActions.length} Available`}
            size="small"
            sx={{
              backgroundColor: 'rgba(0,237,100,0.2)',
              color: primaryColor,
            }}
          />
        </Box>

        {/* Quick Actions Grid */}
        <Grid container spacing={3} sx={{ mb: 4 }}>
          {sortedActions.map((action, idx) => (
            <Grid item xs={12} sm={6} md={4} key={idx}>
              <Card
                sx={{
                  backgroundColor: darkCardBackground,
                  color: 'white',
                  borderRadius: 3,
                  p: 2.5,
                  boxShadow: '0 8px 32px rgba(0,0,0,0.3)',
                  height: '100%',
                  display: 'flex',
                  flexDirection: 'column',
                  justifyContent: 'space-between',
                  border: `1px solid rgba(${getPriorityColor(action.priority).replace('#', '')}, 0.3)`,
                  transition: 'all 0.3s ease',
                  cursor: 'pointer',
                  '&:hover': {
                    transform: 'translateY(-4px)',
                    boxShadow: '0 12px 40px rgba(0,0,0,0.4)',
                    borderColor: getPriorityColor(action.priority),
                  },
                }}
                onClick={() => handleNavigation(action.path)}
              >
                <Box>
                  <Box display="flex" alignItems="center" justifyContent="space-between" mb={2}>
                    <Avatar
                      sx={{
                        bgcolor: 'rgba(0,237,100,0.2)',
                        color: primaryColor,
                        width: 40,
                        height: 40,
                      }}
                    >
                      {action.icon}
                    </Avatar>
                    <Chip
                      label={action.priority.toUpperCase()}
                      size="small"
                      sx={{
                        backgroundColor: `rgba(${getPriorityColor(action.priority).replace('#', '')}, 0.2)`,
                        color: getPriorityColor(action.priority),
                        fontWeight: 'bold',
                        fontSize: '0.7rem',
                      }}
                    />
                  </Box>
                  <Typography variant="h6" gutterBottom fontWeight="bold">
                    {action.title}
                  </Typography>
                  <Typography variant="body2" sx={{ mb: 2, opacity: 0.8, lineHeight: 1.5 }}>
                    {action.desc}
                  </Typography>
                </Box>
                <Button
                  variant="contained"
                  fullWidth
                  sx={{
                    bgcolor: primaryColor,
                    color: 'black',
                    fontWeight: 700,
                    textTransform: 'none',
                    py: 1.5,
                    borderRadius: 2,
                    '&:hover': {
                      bgcolor: '#00C956',
                      transform: 'scale(1.02)',
                    },
                  }}
                  onClick={(e) => {
                    e.stopPropagation();
                    handleNavigation(action.path);
                  }}
                >
                  {action.btn}
                </Button>
              </Card>
            </Grid>
          ))}
        </Grid>

        {/* Footer */}
        <Box sx={{ mt: 'auto', textAlign: 'center', py: 3 }}>
          <Divider sx={{ mb: 2, borderColor: 'rgba(255,255,255,0.1)' }} />
          <Typography variant="body2" sx={{ opacity: 0.6, mb: 1 }}>
            RMS for IP-based Integrated Passenger Information System
          </Typography>
          <Typography variant="caption" sx={{ opacity: 0.4 }}>
            Â© {new Date().getFullYear()} TIC Kolkata | All Rights Reserved
          </Typography>
        </Box>
      </Box>
    </Template>
  );
}